html, body {
    height: 100%;
    /*background-color: #9ca8ba;    */
}

.titlebox {
    padding-top: 30px;
    padding-bottom: 30px;
    text-align: center;
    background-color: white;
}

.titlebox .name {
    font-size: 50px;
}

.titlebox .titleline{
    font-style: italic;
    width: 800px;
    margin: auto auto 0 auto;
    color: #03b1fc;
}

.titlebox .version {
    margin: 0;
    background-color: white;
    border: none;
    color: inherit;
    padding: inherit;
    margin: inherit;
    text-align: center;
    text-decoration: none;
    font-size: inherit;
    display: inline-block;
    outline: none;
}

.recipebox {
    border: 4px solid black;
    width: 800px;
    margin: 20px auto 0 auto
}

.recipe > .title {
    text-align: center;
    font-size: 40px;
    margin-top: 30px;
}

.recipe .ingredients {
    width: 400px;
    font-size: 20px;
    margin: 50px auto 50px auto;
}

.recipe .steps {
    width: 400px;
    font-size: 20px;
    margin: 50px auto 50px auto;
}

.recipebox .regenerateButton {
    width: 200px;
    margin: auto auto 20px auto;
}

.recipebox button {
    width: 200px;
    margin: 0;
    background-color: #03b1fc;
    border: 2px solid white;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    outline: none;
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
}

.recipebox button:hover {
    background-color: white;
    color: black;
    border: 2px solid #03b1fc;
}

.portfolio {
    padding: 10px;
    margin: 0px;
}

.portfolio .floatinglinks {
    margin-top: 15px;
    margin-left: 15px;
    position: fixed;
}
